// menu

.menu-button {
  background-color: rgb(45, 45, 45);
}

//===menu-btn===
.menu-button {
  display: inline-flex;
  margin: 0;
  padding: 0;
  border-radius: 5px;
  border: none;
  background-color: transparent;
  fill: #e3dde266;

  z-index: 1000000;

  @media screen and (min-width: 768px) {
    display: none;
  }
}

.menu-button:hover {
  cursor: pointer;
}

//=============menu-js=============
.menu-button .icon-cross {
  display: none;
}

.menu-button.is-open .icon-menu {
  display: none;
}

.menu-button.is-open .icon-cross {
  display: block;
}

